<template>
    <div data-v-0e624577="" class="page">
        <div data-v-152ab910="" data-v-0e624577="" class="sub-header">
            <div data-v-152ab910="" class="back" @click="$router.go(-1)"> </div>
            <div data-v-152ab910="" class="title">修改密码</div><!----></div>
        <div data-v-0e624577="" class="main">
            <div data-v-0e624577="" class="input-wrap" style="margin-top: 0.3rem;"><input data-v-0e624577=""
                                                                                          placeholder="请输入不小于6位的密码"
                                                                                          type="password"
                                                                                          v-model="password"
                                                                                          class="password">
                <div data-v-0e624577="" class="switch-wrap">
                    <div data-v-0e624577="" role="switch" aria-checked="false" class="van-switch">
                        <div data-v-0e624577="" class="van-switch__node"></div>
                    </div>
                </div>
            </div>
            <div data-v-0e624577="" class="save-btn" @click="onSubmit">提交</div>
        </div>
    </div>
</template>

<script>
    import {Toast} from "vant"
    import Vue from "vue"
    import {mapActions} from "vuex"

    Vue.set(Toast)
    export default {
        data(){
            return {
                password:""
            }
        },
        mounted(){
            document.title=this.$route.meta.title;
        },
        created(){
            //安全认证
            this.$utils.safeUser(this);
            //控制 防止多次请求
            this.isSumit = true;
        },
        methods:{
            ...mapActions({
                getModPassword:"user/getModPassword"
            }),
            onSubmit(){
                if (this.password.match(/^\s*$/)) {
                    Toast("请输入密码");
                    return;
                }
                if (this.isSumit){
                    this.isSumit =false;
                    this.getModPassword({password:this.password,success:(res)=>{
                            if (res.code ===200){
                                this.$router.go(-1)
                            }
                        }})
                }

            }
        }
    }
</script>

<style scoped>
    .page {
        width: 100%;
        min-height: 100vh;
        background-color: #FFFFFF;
    }
    .sub-header {
        width: 100%;
        height: 1rem;
        background-color: #FFFFFF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        border-bottom: 1px solid #EFEFEF;
        position: fixed;
        z-index: 10;
        left: 0;
        top: 0;
        justify-content: space-between;
        padding: 0px 0.1rem;
        box-sizing: border-box
    }

    .sub-header .back {
        width: 0.8rem;
        height: 0.8rem;
        background-image: url("../../../assets/images/home/goods/back.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
    }

    .sub-header .title {
        width: 79%;
        height: auto;
        font-size: 0.32rem;
        text-align: center;
        position: absolute;
        z-index: 1;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%)
    }

    .sub-header .right-btn {
        width: auto;
        height: auto;
        font-size: 0.32rem;
    }

    .main {
        width: 100%;
        padding-top: 1.02rem;
    }

    .main .tip {
        width: 100%;
        height: 1rem;
        background-color: #F3F5C4;
        color: #ac7700;
        font-size: 0.28rem;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
    }

    .main .tip .icon {
        width: 0.4rem;
        height: 0.4rem;
        background-image: url("../../../assets/images/user/mobile/tip.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
        margin-left: 0.4rem;
        margin-right: 0.2rem;
    }

    .main .tip .text {
        width: auto;
        height: auto;
    }

    .main .input-wrap {
        width: 93%;
        height: 0.8rem;
        border: #c5cbcf solid 1px;
        border-radius: 4px;
        margin: 0 auto;
        overflow: hidden;
        display: flex;
        display: -webkit-flex;
    }

    .main .input-wrap .password {
        width: 75%;
        height: 100%;
        margin-left: 0.2rem;
        font-size: 0.28rem;
    }

    .main .input-wrap .switch-wrap {
        width: 20%;
        margin-top: 0.1rem;
    }

    .main .save-btn {
        width: 93%;
        height: 0.8rem;
        background-color: #E42321;
        font-size: 0.28rem;
        color: #FFFFFF;
        text-align: center;
        line-height: 0.8rem;
        margin: 0 auto;
        border-radius: 4px;
        margin-top: 0.2rem;
    }
</style>